,AAAO;EACP,AAAM;EACN,AAAa;EACb,AAAY;EACZ,AAAgB;AAEnB,AAAS;EACN,AAAS;AAEZ,AAAS;EACN,AAAa;AAEhB,AAAS;EACN,AAAK;EACL,AAAO;EACP,AAAa;;;;;;;;;;;;;;AEtoBhB,AAAgB;EACf,AAAS;;;;;AASV,AAAgB;EACf,AAAS;EACT,AAAW;EACX,AAAQ;EAET,AAAgB,AAAQ,AAAY;IACjC,AAAS;IACT,AAAQ;EAGX,AAAgB,AAAQ,AAAG,AAAW,AAAO;IAC1C,AAAY;EAKf,AAAgB,AAAQ,AAAY,AAAQ;IACxC,AAAe;;AAOnB;EAEA,AAAgB;IACb,AAAe;IACf,AAAc;;AAKjB;EAEA,AAAgB;IACb,AAAe;IACf,AAAc;;AAKjB;EAEA,AAAgB;IACb,AAAe;IACf,AAAc;;AAKjB;EAEA,AAAgB;IACb,AAAc;EAKjB,AAAY,AAAgB;IAC1B,AAAc;;;;;AAUhB,AAAe;EACd,AAAc;EACd,AAAe;EACf,AAAa;EACb,AAAe;EACf,AAAY;EACZ,AAAY;EAEb,AAAe,AAAS;IACrB,AAAa;EAEhB,AAAe,AAAS,AAAG;IACxB,AAAS;;AAKZ;EAEA,AAAe;IACZ,AAAc;IACd,AAAa;IACb,AAAY;;AAKf;EAEA,AAAe;IACZ,AAAa;IACb,AAAc;IACd,AAAS;EAMZ,AAAgB,AAAe;IAC7B,AAAS;;;;;AAUX,AAAa,AAAG,AAAW;EACzB,AAAO;;;;;;;;;;ACzIT;EACC,AAAe;EAEhB,AAAe;IACb,AAAW;IACX,AAAa;IACb,AAAgB;IAChB,AAAO;EAGT,AAAe;IACb,AAAe;;AAIjB;EACC,AAAQ;;EACR,AAAU;EACV,AAAY;EACZ,AAAe;EAEhB,AAAgB;IACd,AAAS;IACT,AAAQ;IACR,AAAyB;IACzB,AAA4B;IAC5B,AAAwB;IACxB,AAA2B;IAC3B,AAAkB;IAClB,AAAU;IACV,AAAU","file":"give.css","sourcesContent":["/**\r\n * Clearfix\r\n */\r\n@mixin clearfix() {\r\n  *zoom: 1;\r\n\r\n  &:before,\r\n  &:after {\r\n\tcontent: \" \";\r\n\tdisplay: table;\r\n  }\r\n\r\n  &:after {\r\n\tclear: both;\r\n  }\r\n}\r\n\r\n.give-clearfix {\r\n  @include clearfix;\r\n}\r\n\r\n/**\r\n * Box Sizing\r\n * https://github.com/timrogers/sass-box-sizing-mixin\r\n */\r\n@mixin box-sizing($box-model) {\r\n  -webkit-box-sizing: $box-model; // Safari <= 5\r\n  -moz-box-sizing: $box-model; // Firefox <= 19\r\n  box-sizing: $box-model;\r\n}\r\n\r\n/**\r\n * CSS3 Easing\r\n */\r\n@mixin easing-transition() {\r\n  -moz-transition: 0.2s all linear;\r\n  -o-transition: 0.2s all linear;\r\n  transition: 0.2s all linear;\r\n}\r\n\r\n//Swingin' Loader (from Woo 2.3)\r\n@mixin loader() {\r\n  &:before {\r\n\theight: 1em;\r\n\twidth: 1em;\r\n\tdisplay: block;\r\n\tposition: absolute;\r\n\ttop: 50%;\r\n\tleft: 50%;\r\n\tmargin-left: -.5em;\r\n\tmargin-top: -.5em;\r\n\tdisplay: block;\r\n\t-webkit-animation: spin 1s ease-in-out infinite;\r\n\t-moz-animation: spin 1s ease-in-out infinite;\r\n\tanimation: spin 1s ease-in-out infinite;\r\n\tcontent: \"\";\r\n\tbackground: url(../images/loader.svg) center center;\r\n\tbackground-size: cover;\r\n\tline-height: 1;\r\n\ttext-align: center;\r\n\tfont-size: 2em;\r\n\tcolor: rgba(#000, 0.75);\r\n  }\r\n}\r\n\r\n//Labels\r\n@mixin give-label() {\r\n  padding: .2em .6em .3em;\r\n  font-size: 85%;\r\n  font-weight: 700;\r\n  line-height: 1;\r\n  color: #fff;\r\n  text-align: center;\r\n  white-space: nowrap;\r\n  vertical-align: baseline;\r\n  border-radius: .25em;\r\n}\r\n\r\n@mixin give-label-gray() {\r\n  background-color: #929292;\r\n}\r\n\r\n.give-item-label {\r\n  @include give-label();\r\n}\r\n\r\n.give-item-label-gray {\r\n  @include give-label-gray();\r\n}\r\n\r\n//Fields\r\n@mixin give-currency() {\r\n  background-color: lighten($light-gray, 2%);\r\n  border-top: 1px solid $borders;\r\n  border-bottom: 1px solid $borders;\r\n  margin: 0;\r\n  padding: 0 12px;\r\n  float: left;\r\n  height: 35px;\r\n  line-height: 35px;\r\n  font-size: $font-size-medium;\r\n  @include box-sizing(border-box);\r\n}\r\n\r\n@mixin give-input() {\r\n  float: left;\r\n  border: 1px solid $borders;\r\n  border-radius: 0;\r\n  height: 35px;\r\n  line-height: 35px;\r\n  padding: 0 12px;\r\n  font-size: $font-size-medium;\r\n  @include box-sizing(border-box);\r\n}","/**\r\n * Give Frontend Fontss SCSS\r\n *\r\n * @description: Fonts are from icomoon and the src paths need to be relative to template directory\r\n * @package     Give\r\n * @subpackage  SCSS/Frontend\r\n * @copyright   Copyright (c) 2015, WordImpress\r\n * @license     http://opensource.org/licenses/gpl-2.0.php GNU Public License\r\n*/\r\n@font-face {\r\n  font-family: 'give-icomoon';\r\n  src: url(../assets/fonts/icomoon.eot?-ngjl88);\r\n  src: url(../assets/fonts/icomoon.eot?#iefix-ngjl88) format('embedded-opentype'),\r\n  url(../assets/fonts/icomoon.woff?-ngjl88) format('woff'),\r\n  url(../assets/fonts/icomoon.ttf?-ngjl88) format('truetype'),\r\n  url(../assets/fonts/icomoon.svg?-ngjl88#icomoon) format('svg');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n\r\n@mixin iconstyle() {\r\n  font-family: 'give-icomoon';\r\n  speak: none;\r\n  font-style: normal;\r\n  font-weight: normal;\r\n  font-variant: normal;\r\n  text-transform: none;\r\n  line-height: 1;\r\n\r\n  /* Better Font Rendering =========== */\r\n  -webkit-font-smoothing: antialiased;\r\n  -moz-osx-font-smoothing: grayscale;\r\n}\r\n\r\n[class^=\"give-icon-\"], [class*=\" give-icon-\"] {\r\n  @include iconstyle();\r\n}\r\n.give-icon-lamp:before {\r\n\tcontent: \"\\e603\";\r\n}\r\n\r\n.give-icon-heart:before {\r\n\tcontent: \"\\e604\";\r\n}\r\n\r\n.give-icon-question:before {\r\n\tcontent: \"\\e602\";\r\n}\r\n\r\n.give-icon-info:before {\r\n\tcontent: \"\\e601\";\r\n}\r\n\r\n.give-icon-new-tab:before {\r\n\tcontent: \"\\ea7e\";\r\n}\r\n\r\n.give-icon-alert:before {\r\n\tcontent: \"\\f02d\";\r\n}\r\n\r\n.give-icon-help:before {\r\n\tcontent: \"\\e606\";\r\n}\r\n\r\n","/*\r\n * qTip2 - Pretty powerful tooltips - v2.2.1\r\n * http://qtip2.com\r\n *\r\n * Copyright (c) 2015\r\n * Released under the MIT licenses\r\n * http://jquery.org/license\r\n *\r\n * Date: Mon Sep 8 2014 05:43 EDT-0400\r\n * Plugins: tips\r\n * Styles: core css3\r\n */\r\n.qtip{\r\n\tposition: absolute;\r\n\tleft: -28000px;\r\n\ttop: -28000px;\r\n\tdisplay: none;\r\n\r\n\tmax-width: 280px;\r\n\tmin-width: 50px;\r\n\r\n\tfont-size: 10.5px;\r\n\tline-height: 12px;\r\n\r\n\tdirection: ltr;\r\n\r\n\tbox-shadow: none;\r\n\tpadding: 0;\r\n}\r\n\r\n\t.qtip-content{\r\n\t\tposition: relative;\r\n\t\tpadding: 5px 9px;\r\n\t\toverflow: hidden;\r\n\r\n\t\ttext-align: left;\r\n\t\tword-wrap: break-word;\r\n\t}\r\n\r\n\t.qtip-titlebar{\r\n\t\tposition: relative;\r\n\t\tpadding: 5px 35px 5px 10px;\r\n\t\toverflow: hidden;\r\n\r\n\t\tborder-width: 0 0 1px;\r\n\t\tfont-weight: bold;\r\n\t}\r\n\r\n\t.qtip-titlebar + .qtip-content{ border-top-width: 0 !important; }\r\n\r\n\t/* Default close button class */\r\n\t.qtip-close{\r\n\t\tposition: absolute;\r\n\t\tright: -9px; top: -9px;\r\n\t\tz-index: 11; /* Overlap .qtip-tip */\r\n\r\n\t\tcursor: pointer;\r\n\t\toutline: medium none;\r\n\r\n\t\tborder: 1px solid transparent;\r\n\t}\r\n\r\n\t\t.qtip-titlebar .qtip-close{\r\n\t\t\tright: 4px; top: 50%;\r\n\t\t\tmargin-top: -9px;\r\n\t\t}\r\n\r\n\t\t* html .qtip-titlebar .qtip-close{ top: 16px; } /* IE fix */\r\n\r\n\t\t.qtip-titlebar .ui-icon,\r\n\t\t.qtip-icon .ui-icon{\r\n\t\t\tdisplay: block;\r\n\t\t\ttext-indent: -1000em;\r\n\t\t\tdirection: ltr;\r\n\t\t}\r\n\r\n\t\t.qtip-icon, .qtip-icon .ui-icon{\r\n\t\t\t-moz-border-radius: 3px;\r\n\t\t\t-webkit-border-radius: 3px;\r\n\t\t\tborder-radius: 3px;\r\n\t\t\ttext-decoration: none;\r\n\t\t}\r\n\r\n\t\t\t.qtip-icon .ui-icon{\r\n\t\t\t\twidth: 18px;\r\n\t\t\t\theight: 14px;\r\n\r\n\t\t\t\tline-height: 14px;\r\n\t\t\t\ttext-align: center;\r\n\t\t\t\ttext-indent: 0;\r\n\t\t\t\tfont: normal bold 10px/13px Tahoma,sans-serif;\r\n\r\n\t\t\t\tcolor: inherit;\r\n\t\t\t\tbackground: transparent none no-repeat -100em -100em;\r\n\t\t\t}\r\n\r\n/* Applied to 'focused' tooltips e.g. most recently displayed/interacted with */\r\n.qtip-focus{}\r\n\r\n/* Applied on hover of tooltips i.e. added/removed on mouseenter/mouseleave respectively */\r\n.qtip-hover{}\r\n\r\n/* Default tooltip style */\r\n.qtip-default{\r\n\tborder: 1px solid #F1D031;\r\n\r\n\tbackground-color: #FFFFA3;\r\n\tcolor: #555;\r\n}\r\n\r\n\t.qtip-default .qtip-titlebar{\r\n\t\tbackground-color: #FFEF93;\r\n\t}\r\n\r\n\t.qtip-default .qtip-icon{\r\n\t\tborder-color: #CCC;\r\n\t\tbackground: #F1F1F1;\r\n\t\tcolor: #777;\r\n\t}\r\n\r\n\t.qtip-default .qtip-titlebar .qtip-close{\r\n\t\tborder-color: #AAA;\r\n\t\tcolor: #111;\r\n\t}\r\n\r\n\r\n.qtip-shadow{\r\n\t-webkit-box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\r\n\t-moz-box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\r\n\tbox-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\r\n}\r\n\r\n/* Add rounded corners to your tooltips in: FF3+, Chrome 2+, Opera 10.6+, IE9+, Safari 2+ */\r\n.qtip-rounded,\r\n.qtip-tipsy,\r\n.qtip-bootstrap{\r\n\t-moz-border-radius: 5px;\r\n\t-webkit-border-radius: 5px;\r\n\tborder-radius: 5px;\r\n}\r\n\r\n.qtip-rounded .qtip-titlebar{\r\n\t-moz-border-radius: 4px 4px 0 0;\r\n\t-webkit-border-radius: 4px 4px 0 0;\r\n\tborder-radius: 4px 4px 0 0;\r\n}\r\n\r\n/* Youtube tooltip style */\r\n.qtip-youtube{\r\n\t-moz-border-radius: 2px;\r\n\t-webkit-border-radius: 2px;\r\n\tborder-radius: 2px;\r\n\r\n\t-webkit-box-shadow: 0 0 3px #333;\r\n\t-moz-box-shadow: 0 0 3px #333;\r\n\tbox-shadow: 0 0 3px #333;\r\n\r\n\tcolor: white;\r\n\tborder: 0 solid transparent;\r\n\r\n\tbackground: #4A4A4A;\r\n\tbackground-image: -webkit-gradient(linear,left top,left bottom,color-stop(0,#4A4A4A),color-stop(100%,black));\r\n\tbackground-image: -webkit-linear-gradient(top,#4A4A4A 0,black 100%);\r\n\tbackground-image: -moz-linear-gradient(top,#4A4A4A 0,black 100%);\r\n\tbackground-image: -ms-linear-gradient(top,#4A4A4A 0,black 100%);\r\n\tbackground-image: -o-linear-gradient(top,#4A4A4A 0,black 100%);\r\n}\r\n\r\n\t.qtip-youtube .qtip-titlebar{\r\n\t\tbackground-color: #4A4A4A;\r\n\t\tbackground-color: rgba(0,0,0,0);\r\n\t}\r\n\r\n\t.qtip-youtube .qtip-content{\r\n\t\tpadding: .75em;\r\n\t\tfont: 12px arial,sans-serif;\r\n\r\n\t\tfilter: progid:DXImageTransform.Microsoft.Gradient(GradientType=0,StartColorStr=#4a4a4a,EndColorStr=#000000);\r\n\t\t-ms-filter: \"progid:DXImageTransform.Microsoft.Gradient(GradientType=0,StartColorStr=#4a4a4a,EndColorStr=#000000);\";\r\n\t}\r\n\r\n\t.qtip-youtube .qtip-icon{\r\n\t\tborder-color: #222;\r\n\t}\r\n\r\n\t.qtip-youtube .qtip-titlebar .ui-state-hover{\r\n\t\tborder-color: #303030;\r\n\t}\r\n\r\n\r\n/* jQuery TOOLS Tooltip style */\r\n.qtip-jtools{\r\n\tbackground: #232323;\r\n\tbackground: rgba(0, 0, 0, 0.7);\r\n\tbackground-image: -webkit-gradient(linear, left top, left bottom, from(#717171), to(#232323));\r\n\tbackground-image: -moz-linear-gradient(top, #717171, #232323);\r\n\tbackground-image: -webkit-linear-gradient(top, #717171, #232323);\r\n\tbackground-image: -ms-linear-gradient(top, #717171, #232323);\r\n\tbackground-image: -o-linear-gradient(top, #717171, #232323);\r\n\r\n\tborder: 2px solid #ddd;\r\n\tborder: 2px solid rgba(241,241,241,1);\r\n\r\n\t-moz-border-radius: 2px;\r\n\t-webkit-border-radius: 2px;\r\n\tborder-radius: 2px;\r\n\r\n\t-webkit-box-shadow: 0 0 12px #333;\r\n\t-moz-box-shadow: 0 0 12px #333;\r\n\tbox-shadow: 0 0 12px #333;\r\n}\r\n\r\n\t/* IE Specific */\r\n\t.qtip-jtools .qtip-titlebar{\r\n\t\tbackground-color: transparent;\r\n\t\tfilter:progid:DXImageTransform.Microsoft.gradient(startColorstr=#717171,endColorstr=#4A4A4A);\r\n\t\t-ms-filter: \"progid:DXImageTransform.Microsoft.gradient(startColorstr=#717171,endColorstr=#4A4A4A)\";\r\n\t}\r\n\t.qtip-jtools .qtip-content{\r\n\t\tfilter:progid:DXImageTransform.Microsoft.gradient(startColorstr=#4A4A4A,endColorstr=#232323);\r\n\t\t-ms-filter: \"progid:DXImageTransform.Microsoft.gradient(startColorstr=#4A4A4A,endColorstr=#232323)\";\r\n\t}\r\n\r\n\t.qtip-jtools .qtip-titlebar,\r\n\t.qtip-jtools .qtip-content{\r\n\t\tbackground: transparent;\r\n\t\tcolor: white;\r\n\t\tborder: 0 dashed transparent;\r\n\t}\r\n\r\n\t.qtip-jtools .qtip-icon{\r\n\t\tborder-color: #555;\r\n\t}\r\n\r\n\t.qtip-jtools .qtip-titlebar .ui-state-hover{\r\n\t\tborder-color: #333;\r\n\t}\r\n\r\n\r\n/* Cluetip style */\r\n.qtip-cluetip{\r\n\t-webkit-box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.4);\r\n\t-moz-box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.4);\r\n\tbox-shadow: 4px 4px 5px rgba(0, 0, 0, 0.4);\r\n\r\n\tbackground-color: #D9D9C2;\r\n\tcolor: #111;\r\n\tborder: 0 dashed transparent;\r\n}\r\n\r\n\t.qtip-cluetip .qtip-titlebar{\r\n\t\tbackground-color: #87876A;\r\n\t\tcolor: white;\r\n\t\tborder: 0 dashed transparent;\r\n\t}\r\n\r\n\t.qtip-cluetip .qtip-icon{\r\n\t\tborder-color: #808064;\r\n\t}\r\n\r\n\t.qtip-cluetip .qtip-titlebar .ui-state-hover{\r\n\t\tborder-color: #696952;\r\n\t\tcolor: #696952;\r\n\t}\r\n\r\n\r\n/* Tipsy style */\r\n.qtip-tipsy{\r\n\tbackground: black;\r\n\tbackground: rgba(0, 0, 0, .87);\r\n\r\n\tcolor: white;\r\n\tborder: 0 solid transparent;\r\n\r\n\tfont-size: 11px;\r\n\tfont-family: 'Lucida Grande', sans-serif;\r\n\tfont-weight: bold;\r\n\tline-height: 16px;\r\n\ttext-shadow: 0 1px black;\r\n}\r\n\r\n\t.qtip-tipsy .qtip-titlebar{\r\n\t\tpadding: 6px 35px 0 10px;\r\n\t\tbackground-color: transparent;\r\n\t}\r\n\r\n\t.qtip-tipsy .qtip-content{\r\n\t\tpadding: 6px 10px;\r\n\t}\r\n\r\n\t.qtip-tipsy .qtip-icon{\r\n\t\tborder-color: #222;\r\n\t\ttext-shadow: none;\r\n\t}\r\n\r\n\t.qtip-tipsy .qtip-titlebar .ui-state-hover{\r\n\t\tborder-color: #303030;\r\n\t}\r\n\r\n\r\n/* Tipped style */\r\n.qtip-tipped{\r\n\tborder: 3px solid #959FA9;\r\n\r\n\t-moz-border-radius: 3px;\r\n\t-webkit-border-radius: 3px;\r\n\tborder-radius: 3px;\r\n\r\n\tbackground-color: #F9F9F9;\r\n\tcolor: #454545;\r\n\r\n\tfont-weight: normal;\r\n\tfont-family: serif;\r\n}\r\n\r\n\t.qtip-tipped .qtip-titlebar{\r\n\t\tborder-bottom-width: 0;\r\n\r\n\t\tcolor: white;\r\n\t\tbackground: #3A79B8;\r\n\t\tbackground-image: -webkit-gradient(linear, left top, left bottom, from(#3A79B8), to(#2E629D));\r\n\t\tbackground-image: -webkit-linear-gradient(top, #3A79B8, #2E629D);\r\n\t\tbackground-image: -moz-linear-gradient(top, #3A79B8, #2E629D);\r\n\t\tbackground-image: -ms-linear-gradient(top, #3A79B8, #2E629D);\r\n\t\tbackground-image: -o-linear-gradient(top, #3A79B8, #2E629D);\r\n\t\tfilter:progid:DXImageTransform.Microsoft.gradient(startColorstr=#3A79B8,endColorstr=#2E629D);\r\n\t\t-ms-filter: \"progid:DXImageTransform.Microsoft.gradient(startColorstr=#3A79B8,endColorstr=#2E629D)\";\r\n\t}\r\n\r\n\t.qtip-tipped .qtip-icon{\r\n\t\tborder: 2px solid #285589;\r\n\t\tbackground: #285589;\r\n\t}\r\n\r\n\t\t.qtip-tipped .qtip-icon .ui-icon{\r\n\t\t\tbackground-color: #FBFBFB;\r\n\t\t\tcolor: #555;\r\n\t\t}\r\n\r\n\r\n/**\r\n * Twitter Bootstrap style.\r\n *\r\n * Tested with IE 8, IE 9, Chrome 18, Firefox 9, Opera 11.\r\n * Does not work with IE 7.\r\n */\r\n.qtip-bootstrap{\r\n\t/** Taken from Bootstrap body */\r\n\tfont-size: 14px;\r\n\tline-height: 20px;\r\n\tcolor: #333333;\r\n\r\n\t/** Taken from Bootstrap .popover */\r\n\tpadding: 1px;\r\n\tbackground-color: #ffffff;\r\n\tborder: 1px solid #ccc;\r\n\tborder: 1px solid rgba(0, 0, 0, 0.2);\r\n\t-webkit-border-radius: 6px;\r\n\t-moz-border-radius: 6px;\r\n\tborder-radius: 6px;\r\n\t-webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);\r\n\t-moz-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);\r\n\tbox-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);\r\n\t-webkit-background-clip: padding-box;\r\n\t-moz-background-clip: padding;\r\n\tbackground-clip: padding-box;\r\n}\r\n\r\n\t.qtip-bootstrap .qtip-titlebar{\r\n\t\t/** Taken from Bootstrap .popover-title */\r\n\t\tpadding: 8px 14px;\r\n\t\tmargin: 0;\r\n\t\tfont-size: 14px;\r\n\t\tfont-weight: normal;\r\n\t\tline-height: 18px;\r\n\t\tbackground-color: #f7f7f7;\r\n\t\tborder-bottom: 1px solid #ebebeb;\r\n\t\t-webkit-border-radius: 5px 5px 0 0;\r\n\t\t-moz-border-radius: 5px 5px 0 0;\r\n\t\tborder-radius: 5px 5px 0 0;\r\n\t}\r\n\r\n\t\t.qtip-bootstrap .qtip-titlebar .qtip-close{\r\n\t\t\t/**\r\n\t\t\t * Overrides qTip2:\r\n\t\t\t * .qtip-titlebar .qtip-close{\r\n\t\t\t *   [...]\r\n\t\t\t *   right: 4px;\r\n\t\t\t *   top: 50%;\r\n\t\t\t *   [...]\r\n\t\t\t *   border-style: solid;\r\n\t\t\t * }\r\n\t\t\t */\r\n\t\t\tright: 11px;\r\n\t\t\ttop: 45%;\r\n\t\t\tborder-style: none;\r\n\t\t}\r\n\r\n\t.qtip-bootstrap .qtip-content{\r\n\t\t/** Taken from Bootstrap .popover-content */\r\n\t\tpadding: 9px 14px;\r\n\t}\r\n\r\n\t.qtip-bootstrap .qtip-icon{\r\n\t\t/**\r\n\t\t * Overrides qTip2:\r\n\t\t * .qtip-default .qtip-icon {\r\n\t\t *   border-color: #CCC;\r\n\t\t *   background: #F1F1F1;\r\n\t\t *   color: #777;\r\n\t\t * }\r\n\t\t */\r\n\t\tbackground: transparent;\r\n\t}\r\n\r\n\t\t.qtip-bootstrap .qtip-icon .ui-icon{\r\n\t\t\t/**\r\n\t\t\t * Overrides qTip2:\r\n\t\t\t * .qtip-icon .ui-icon{\r\n\t\t\t *   width: 18px;\r\n\t\t\t *   height: 14px;\r\n\t\t\t * }\r\n\t\t\t */\r\n\t\t\twidth: auto;\r\n\t\t\theight: auto;\r\n\r\n\t\t\t/* Taken from Bootstrap .close */\r\n\t\t\tfloat: right;\r\n\t\t\tfont-size: 20px;\r\n\t\t\tfont-weight: bold;\r\n\t\t\tline-height: 18px;\r\n\t\t\tcolor: #000000;\r\n\t\t\ttext-shadow: 0 1px 0 #ffffff;\r\n\t\t\topacity: 0.2;\r\n\t\t\tfilter: alpha(opacity=20);\r\n\t\t}\r\n\r\n\t\t.qtip-bootstrap .qtip-icon .ui-icon:hover{\r\n\t\t\t/* Taken from Bootstrap .close:hover */\r\n\t\t\tcolor: #000000;\r\n\t\t\ttext-decoration: none;\r\n\t\t\tcursor: pointer;\r\n\t\t\topacity: 0.4;\r\n\t\t\tfilter: alpha(opacity=40);\r\n\t\t}\r\n\r\n\r\n/* IE9 fix - removes all filters */\r\n.qtip:not(.ie9haxors) div.qtip-content,\r\n.qtip:not(.ie9haxors) div.qtip-titlebar{\r\n\tfilter: none;\r\n\t-ms-filter: none;\r\n}\r\n\r\n\r\n.qtip .qtip-tip{\r\n\tmargin: 0 auto;\r\n\toverflow: hidden;\r\n\tz-index: 10;\r\n\r\n}\r\n\r\n\t/* Opera bug #357 - Incorrect tip position\r\n\thttps://github.com/Craga89/qTip2/issues/367 */\r\n\tx:-o-prefocus, .qtip .qtip-tip{\r\n\t\tvisibility: hidden;\r\n\t}\r\n\r\n\t.qtip .qtip-tip,\r\n\t.qtip .qtip-tip .qtip-vml,\r\n\t.qtip .qtip-tip canvas{\r\n\t\tposition: absolute;\r\n\r\n\t\tcolor: #123456;\r\n\t\tbackground: transparent;\r\n\t\tborder: 0 dashed transparent;\r\n\t}\r\n\r\n\t.qtip .qtip-tip canvas{ top: 0; left: 0; }\r\n\r\n\t.qtip .qtip-tip .qtip-vml{\r\n\t\tbehavior: url(#default#VML);\r\n\t\tdisplay: inline-block;\r\n\t\tvisibility: visible;\r\n\t}\r\n","/**\r\n * Give Frontend Receipt SCSS\r\n *\r\n * @package     Give\r\n * @subpackage  SCSS/Frontend\r\n * @copyright   Copyright (c) 2015, WordImpress\r\n * @license     http://opensource.org/licenses/gpl-2.0.php GNU Public License\r\n*/\r\n\r\n\r\n/* =Receipt Page\r\n--